.boxNameImg{
    position: fixed;
    top: -95rem;
    background-color: #fff;
    padding: 2rem;
    left: 0;
    right: 0;
    width: 65%;
    margin-left: auto;
    margin-right: auto;
    border-radius: 2rem;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
    transition: top .5s ease;
}

.boton input{
    width: 100%;
    height: 4rem;
    display: block;
    border: none;
    color: #fff;
    text-transform: uppercase;
}