.logo img{
    width: 85%;
    max-width: 35rem;
    margin-left: auto;
    margin-right: auto;
    margin-top: 1rem;
    margin-bottom: 2rem;
}
.inicio{
    background-image: url('/build/img/bg-inicio.webp');
    background-position: center center;
    background-size: cover;
    background-repeat: no-repeat;
    height: 100vh;
}
.box-form-registro{
    position: fixed;
    transition: top .5s ease;
    padding-top: 1rem;
    top: -95rem;
}
.box-form-sesion{
    position: fixed;
    transition: top .5s ease;
    padding-top: 10rem;
}

@include tablet{
    .box-form-registro, .box-form-sesion{
        left: 25rem;
        margin-top: 15rem;
    }
}
.form{
    width: 85%;
    max-width: 35rem;
    max-height: 50rem;
    margin-left: auto;
    margin-right: auto;
    z-index: 20;
    background-color: #fff;
    border-radius: 3rem;
    padding: 2rem;

}
.form h2, .form p{
    margin: 0;
}
.form h2{
    font-size: 25px;
}
.form p{
    text-align: center;
    font-size: 15px;
}
.close-form{
    position: absolute;
    right: 3rem;
    cursor: pointer;
}
.campo{
    width: 100%;
    margin-bottom: 1rem;
    display: flex;
    flex-direction: column;
}
.campo label{
    margin-left: auto;
    margin-right: auto;
}
.campo input{
    height: 4rem;
}
.campo textarea{
    height: 12rem;
    resize: none;
}
.submit input{
    width: 100%;
    height: 4rem;
    display: block;
    border: none;
    color: #fff;
    text-transform: uppercase;
    pointer-events: none;
}

.blue{
    padding: 1rem;
    color: $azul;
}
.blue a{
    color: $azul;
    font-weight: 700;
}

.error{
    color: #8b0000;
    font-weight: $bold;
    text-align: center;
}